
.left-panel {
  width: 350px;
  float: left;
}
.left-panel-title {
  margin: 5px 5px;
}
.switch-tag-btn {
  color: #4fbc7d;
  height: 24px;
  padding: 2px 10px;
  font-size: 12px;
  border: 1px solid #4fbc7d;
  background-color: #ffffff;
  margin-left: -1px;
  outline: none;
  cursor: pointer;
}
.tab-button-group {
  margin: 3px 15px;
}
.tab-active {
  background-color: #4fbc7d;
  color: #fff;
}
.left-panel-select-box {
  border-top: 1px solid #d2d2d2;
  width: 350px;
  height: 236px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0;
  background: #efefef;
  margin: 15px 0 1px 0;
}
.cas-panel {
  margin: 0;
  padding: 0;
}
.cas-item {
  font-size: 14px;
  padding: 5px 5px 5px 15px;
  cursor: pointer;
  list-style: none;
}
.cas-item:hover {
  color: #fff;
  background-color: #4db262;
}




