
.disabled {
  color: #b4b4b4;
  cursor: not-allowed !important;
}
.disabled:hover {
  color: #b4b4b4;
  cursor: not-allowed !important;
  background-color: #d3d6d4 !important;
}
.index {
  width: 30px;
  height: 308px;
  background-color: #dbdedc;
  font-size: 12px;
  float: left;
  overflow-y: auto;
}
::-webkit-scrollbar{width: 0
}
.index ul {
  margin: 0;
  padding: 5px 0 0 0;
  list-style: none;
}
.index ul li {
  padding: 1px 0 1px 8px;
}
.index ul li:hover {
  cursor: pointer;
  color: #fff;
  background-color: #3eb368;
}



