
.select-tag-container {
  position: absolute;
  top: 30px;
  left: 50%;
  margin-left: -400px;
  width: 800px;
  height: 488px;
  z-index: 101;
  /*box-shadow:2px 4px 10px 0 rgba(127,127,127,0.50);*/
  border: 2px solid #3eb368;
  border-radius: 3px;
  background-color: #ffffff;
}
.title {
  background-color: #3eb368;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
  width: 100%;
  color: #fff;
  text-align: left;
  padding: 10px 0 0 20px;
  font-size: 14px;
}
.close {
  float: right;
  padding: 3px 10px;
  margin-right: 10px;
}
.button-group {
  margin: 20px 0 26px 20px;
}
.btn {
  color: #4fbc7d;
  width: 86px;
  height: 34px;
  font-size: 13px;
  border: 1px solid #4fbc7d;
  background-color: #ffffff;
  margin-left: -5px;
  outline: none;
  cursor: pointer;
}
.btn-active {
  background-color: #4fbc7d;
  color: #fff;
}
.footer {
  text-align: center;
}
.footer-btn {
  border-radius: 7px;
  margin: 15px;
}
.btn:hover {
  color: #fff;
  background-color: #3eb368;
}




