
.right-panel-container {
  width: 380px;
  display: inline-block;
}
.list-title {
  font-size: 14px;
  border-bottom: 1px solid #d2d2d2;
  padding: 0 0 5px 10px;
}
.first-list {
  width: 190px;
  height: 262px;
  overflow-y: auto;
  display: inline-block;
}
.first-list::-webkit-scrollbar {
  width: 0
}
.second-list {
  float: right;
  width: 190px;
  height: 262px;
  overflow-y: auto;
  display: inline-block;
}
.second-list::-webkit-scrollbar {
  width: 0
}
.level1-list {
  height: 262px;
  overflow-y: auto;
}
.level1-list::-webkit-scrollbar {
  width: 0;
}



